//
//Generalformcontrols(plusafewspecifichigh-levelint erventions)
//

.form-control{
display:block;
width:100%;
padding:$input-padding-y$input-padding-x;
font-family:$input-font-family;
@includefont-size($input-font-size);
font-weight:$input-font-weight;
line-height:$input-line-height;
color:$input-color;
background-color:$input-bg;
background-clip:padding-box;
border:$input-border-widthsolid$input-border-color;
appearance:none;//FixappearancefordateinputsinSafari

//Note:Thishasnoeffecton<select>sinsomebrowsers,duetothelimitedstylabilityof`<select>`sinCSS.
@includeborder-radius($input-border-radius,0);

@includebox-shadow($input-box-shadow);
@includetransition($input-transition);

&[type="file"]{
overflow:hidden;//preventpseudoelementbuttonoverlap

&:not(:disabled):not([readonly ]){
cursor:point er;
}
}

//Customizethe`:focus`statetoimitatenativeWebKitstyles.
&:focus{
color:$input-focus-color;
background-color:$input-focus-bg;
border-color:$input-focus-border-color;
outline:0;
@if$enable-shadows{
@includebox-shadow($input-box-shadow,$input-focus-box-shadow);
}@else{
//Avoidusing mixinsowecanpasscustomfocusshadowproperly
box-shadow:$input-focus-box-shadow;
}
}

//AddsomeheighttodateinputsoniOS
//https://github.com/twbs/bootstrap/issues/23307
//TODO:wecanremovethisworkaroundoncehttps://bugs.webkit.org/show_bug.cgi?id=198959isresolved
&::-webkit-date-and-time-value{
//Multiplyline-heightby1emifithasnounit
height:if(unit($input-line-height)=="",$input-line-height*1em,$input-line-height);
}

//Placeholder
&::placeholder{
color:$input-placeholder-color;
//OverrideFirefox'sunusualdefaultopacity;seehttps://github.com/twbs/bootstrap/pull/11526.
opacity:1;
}

//Disabledandread-onlyinputs
//
//HTML5saysthatcontrolsunderafieldset>legend:first-childwon'tbe
//disabledifthefieldsetisdisabled.Duetoimplementationdifficulty,we
//don'thonorthatedgecase;westylethemasdisabledanyway.
&:disabled,
&[readonly ]{
background-color:$input-disabled-bg;
border-color:$input-disabled-border-color;
//iOSfixforunreadabledisabledcontent;seehttps://github.com/twbs/bootstrap/issues/11655.
opacity:1;
}

//Fileinputbuttonstheming
&::file-selector-button{
padding:$input-padding-y$input-padding-x;
margin:(-$input-padding-y)(-$input-padding-x);
margin-inline-end:$input-padding-x;
color:$form-file-button-color;
@includegradient-bg($form-file-button-bg);
point er-events:none;
border-color:inherit;
border-style:solid;
border-width:0;
border-inline-end-width:$input-border-width;
border-radius:0;//stylelint -disable-lineproperty-disallowed-list
@includetransition($btn-transition);
}

&:hover:not(:disabled):not([readonly ])::file-selector-button{
background-color:$form-file-button-hover-bg;
}

&::-webkit-file-upload-button{
padding:$input-padding-y$input-padding-x;
margin:(-$input-padding-y)(-$input-padding-x);
margin-inline-end:$input-padding-x;
color:$form-file-button-color;
@includegradient-bg($form-file-button-bg);
point er-events:none;
border-color:inherit;
border-style:solid;
border-width:0;
border-inline-end-width:$input-border-width;
border-radius:0;//stylelint -disable-lineproperty-disallowed-list
@includetransition($btn-transition);
}

&:hover:not(:disabled):not([readonly ])::-webkit-file-upload-button{
background-color:$form-file-button-hover-bg;
}
}

//readonly controlsasplaint ext
//
//Applyclass toareadonly inputtomakeitappearlikeregularplain
//text(withoutanyborder,backgroundcolor,focusindicator)

.form-control-plaint ext{
display:block;
width:100%;
padding:$input-padding-y0;
margin-bottom:0;//matchinputsifthisclass comesoninputswithdefaultmargins
line-height:$input-line-height;
color:$input-plaint ext-color;
background-color:transparent;
border:solidtransparent;
border-width:$input-border-width0;

&.form-control-sm,
&.form-control-lg{
padding-right:0;
padding-left:0;
}
}

//Formcontrolsizing
//
//Buildon`.form-control`withmodifierclass estodecreaseorincreasethe
//heightandfont-sizeofformcontrols.
//
//Repeatedin`_input_group.scss`toavoidSassextendissues.

.form-control-sm{
min-height:$input-height-sm;
padding:$input-padding-y-sm$input-padding-x-sm;
@includefont-size($input-font-size-sm);
@includeborder-radius($input-border-radius-sm);

&::file-selector-button{
padding:$input-padding-y-sm$input-padding-x-sm;
margin:(-$input-padding-y-sm)(-$input-padding-x-sm);
margin-inline-end:$input-padding-x-sm;
}

&::-webkit-file-upload-button{
padding:$input-padding-y-sm$input-padding-x-sm;
margin:(-$input-padding-y-sm)(-$input-padding-x-sm);
margin-inline-end:$input-padding-x-sm;
}
}

.form-control-lg{
min-height:$input-height-lg;
padding:$input-padding-y-lg$input-padding-x-lg;
@includefont-size($input-font-size-lg);
@includeborder-radius($input-border-radius-lg);

&::file-selector-button{
padding:$input-padding-y-lg$input-padding-x-lg;
margin:(-$input-padding-y-lg)(-$input-padding-x-lg);
margin-inline-end:$input-padding-x-lg;
}

&::-webkit-file-upload-button{
padding:$input-padding-y-lg$input-padding-x-lg;
margin:(-$input-padding-y-lg)(-$input-padding-x-lg);
margin-inline-end:$input-padding-x-lg;
}
}

//Makesuretextareasdon'tshrinktoomuchwhenresized
//https://github.com/twbs/bootstrap/pull/29124
//stylelint -disableselector-no-qualifying-type
textarea{
&.form-control{
min-height:$input-height;
}

&.form-control-sm{
min-height:$input-height-sm;
}

&.form-control-lg{
min-height:$input-height-lg;
}
}
//stylelint -enableselector-no-qualifying-type

.form-control-color{
width:$form-color-width;
height:auto;//Overridefixedbrowserheight
padding:$input-padding-y;

&:not(:disabled):not([readonly ]){
cursor:point er;
}

&::-moz-color-swatch{
height:if(unit($input-line-height)=="",$input-line-height*1em,$input-line-height);
@includeborder-radius($input-border-radius);
}

&::-webkit-color-swatch{
height:if(unit($input-line-height)=="",$input-line-height*1em,$input-line-height);
@includeborder-radius($input-border-radius);
}
}
