//
//Basestyles
//

.card{
position:relative;
display:flex;
flex-direction:column;
min-width:0;//Seehttps://github.com/twbs/bootstrap/pull/22740#issuecomment-305868106
height:$card-height;
word-wrap:break-word;
background-color:$card-bg;
background-clip:border-box;
border:$card-border-widthsolid$card-border-color;
@includeborder-radius($card-border-radius);
@includebox-shadow($card-box-shadow);

>hr{
margin-right:0;
margin-left:0;
}

>.list-group{
border-top:inherit;
border-bottom:inherit;

&:first-child{
border-top-width:0;
@includeborder-top-radius($card-inner-border-radius);
}

&:last-child{
border-bottom-width:0;
@includeborder-bottom-radius($card-inner-border-radius);
}
}

//Duetospecificityoftheaboveselector(`.card>.list-group`),wemust
//useachildselectorheretopreventdoubleborders.
>.card-header+.list-group,
>.list-group+.card-footer{
border-top:0;
}
}

.card-body{
//Enable`flex-grow:1`fordecksandgroupssothatcardblockstakeup
//asmuchspaceaspossible,ensuringfootersarealignedtothebottom.
flex:11auto;
padding:$card-spacer-y$card-spacer-x;
color:$card-color;
}

.card-title{
margin-bottom:$card-title-spacer-y;
}

.card-subtitle{
margin-top:-$card-title-spacer-y*.5;
margin-bottom:0;
}

.card-text:last-child{
margin-bottom:0;
}

.card-link{
&:hover{
text-decoration:if($link-hover-decoration==underline,none,null);
}

+.card-link{
margin-left:$card-spacer-x;
}
}

//
//Optionaltextualcaps
//

.card-header{
padding:$card-cap-padding-y$card-cap-padding-x;
margin-bottom:0;//Removesthedefaultmargin-bottomof<hN>
color:$card-cap-color;
background-color:$card-cap-bg;
border-bottom:$card-border-widthsolid$card-border-color;

&:first-child{
@includeborder-radius($card-inner-border-radius$card-inner-border-radius00);
}
}

.card-footer{
padding:$card-cap-padding-y$card-cap-padding-x;
color:$card-cap-color;
background-color:$card-cap-bg;
border-top:$card-border-widthsolid$card-border-color;

&:last-child{
@includeborder-radius(00$card-inner-border-radius$card-inner-border-radius);
}
}


//
//Headernavs
//

.card-header-tabs{
margin-right:-$card-cap-padding-x*.5;
margin-bottom:-$card-cap-padding-y;
margin-left:-$card-cap-padding-x*.5;
border-bottom:0;

@if$nav-tabs-link-active-bg!=$card-bg{
.nav-link.active{
background-color:$card-bg;
border-bottom-color:$card-bg;
}
}
}

.card-header-pills{
margin-right:-$card-cap-padding-x*.5;
margin-left:-$card-cap-padding-x*.5;
}

//Cardimage
.card-img-overlay{
position:absolute;
top:0;
right:0;
bottom:0;
left:0;
padding:$card-img-overlay-padding;
@includeborder-radius($card-inner-border-radius);
}

.card-img,
.card-img-top,
.card-img-bottom{
width:100%;//Requiredbecauseweuseflexboxandthisinherentlyappliesalign-self:stretch
}

.card-img,
.card-img-top{
@includeborder-top-radius($card-inner-border-radius);
}

.card-img,
.card-img-bottom{
@includeborder-bottom-radius($card-inner-border-radius);
}


//
//Cardgroups
//

.card-group{
//Thechildselectorallowsnested`.card`within`.card-group`
//todisplayproperly.
>.card{
margin-bottom:$card-group-margin;
}

@includemedia-breakpoint -up(sm){
display:flex;
flex-flow:rowwrap;
//Thechildselectorallowsnested`.card`within`.card-group`
//todisplayproperly.
>.card{
//Flexbugs#4:https://github.com/philipwalton/flexbugs#flexbug-4
flex:100%;
margin-bottom:0;

+.card{
margin-left:0;
border-left:0;
}

//Handleroundedcorners
@if$enable-rounded{
&:not(:last-child){
@includeborder-end-radius(0);

.card-img-top,
.card-header{
//stylelint -disable-next-lineproperty-disallowed-list
border-top-right-radius:0;
}
.card-img-bottom,
.card-footer{
//stylelint -disable-next-lineproperty-disallowed-list
border-bottom-right-radius:0;
}
}

&:not(:first-child){
@includeborder-start-radius(0);

.card-img-top,
.card-header{
//stylelint -disable-next-lineproperty-disallowed-list
border-top-left-radius:0;
}
.card-img-bottom,
.card-footer{
//stylelint -disable-next-lineproperty-disallowed-list
border-bottom-left-radius:0;
}
}
}
}
}
}
