//Containerthatthemodalscrollswithin
.modal{
position:fixed;
top:0;
left:0;
z-index:$zindex-modal;
display:none;
width:100%;
height:100%;
overflow-x:hidden;
overflow-y:auto;
//PreventChromeonWindowsfromaddingafocusoutline.Fordetails,see
//https://github.com/twbs/bootstrap/pull/10951.
outline:0;
//Wedeliberatelydon'tuse`-webkit-overflow-scrolling:touch;`duetoa
//gnarlyiOSSafaribug:https://bugs.webkit.org/show_bug.cgi?id=158342
//Seealsohttps://github.com/twbs/bootstrap/issues/17695
}

//Shelldivtopositionthemodalwithbottompadding
.modal-dialog{
position:relative;
width:auto;
margin:$modal-dialog-margin;
//allowclickstopassthroughforcustomclickhandlingtoclosemodal
point er-events:none;

//Whenfadingint hemodal,animateittoslidedown
.modal.fade&{
@includetransition($modal-transition);
transform:$modal-fade-transform;
}
.modal.show&{
transform:$modal-show-transform;
}

//Whentryingtoclose,animatefocustoscale
.modal.modal-static&{
transform:$modal-scale-transform;
}
}

.modal-dialog-scrollable{
height:subtract(100%,$modal-dialog-margin*2);

.modal-content{
max-height:100%;
overflow:hidden;
}

.modal-body{
overflow-y:auto;
}
}

.modal-dialog-centered{
display:flex;
align-items:center;
min-height:subtract(100%,$modal-dialog-margin*2);
}

//Actualmodal
.modal-content{
position:relative;
display:flex;
flex-direction:column;
width:100%;//Ensure`.modal-content`extendsthefullwidthoftheparent`.modal-dialog`
//counteractthepoint er-events:none;int he.modal-dialog
color:$modal-content-color;
point er-events:auto;
background-color:$modal-content-bg;
background-clip:padding-box;
border:$modal-content-border-widthsolid$modal-content-border-color;
@includeborder-radius($modal-content-border-radius);
@includebox-shadow($modal-content-box-shadow-xs);
//Removefocusoutlinefromopenedmodal
outline:0;
}

//Modalbackground
.modal-backdrop{
@includeoverlay-backdrop($zindex-modal-backdrop,$modal-backdrop-bg,$modal-backdrop-opacity);
}

//Modalheader
//Topsectionofthemodalw/titleanddismiss
.modal-header{
display:flex;
flex-shrink:0;
align-items:center;
justify-content:space-between;//Putmodalheaderelements(titleanddismiss)onoppositeends
padding:$modal-header-padding;
border-bottom:$modal-header-border-widthsolid$modal-header-border-color;
@includeborder-top-radius($modal-content-inner-border-radius);

.btn-close{
padding:($modal-header-padding-y*.5)($modal-header-padding-x*.5);
margin:($modal-header-padding-y*-.5)($modal-header-padding-x*-.5)($modal-header-padding-y*-.5)auto;
}
}

//Titletextwithinheader
.modal-title{
margin-bottom:0;
line-height:$modal-title-line-height;
}

//Modalbody
//Whereallmodalcontentresides(siblingof.modal-headerand.modal-footer)
.modal-body{
position:relative;
//Enable`flex-grow:1`sothatthebodytakeupasmuchspaceaspossible
//whenthereshouldbeafixedheighton`.modal-dialog`.
flex:11auto;
padding:$modal-inner-padding;
}

//Footer(foractions)
.modal-footer{
display:flex;
flex-wrap:wrap;
flex-shrink:0;
align-items:center;//verticallycenter
justify-content:flex-end;//Rightalignbuttonswithflexpropertybecausetext-aligndoesn'tworkonflexitems
padding:$modal-inner-padding-$modal-footer-margin-between*.5;
border-top:$modal-footer-border-widthsolid$modal-footer-border-color;
@includeborder-bottom-radius($modal-content-inner-border-radius);

//Placemarginbetweenfooterelements
//Thissolutionisfarfromidealbecauseoftheuniversalselectorusage,
//butisneededtofixhttps://github.com/twbs/bootstrap/issues/24800
>*{
margin:$modal-footer-margin-between*.5;
}
}

//Scaleupthemodal
@includemedia-breakpoint -up(sm){
//Automaticallysetmodal'swidthforlargerviewports
.modal-dialog{
max-width:$modal-md;
margin:$modal-dialog-margin-y-sm-upauto;
}

.modal-dialog-scrollable{
height:subtract(100%,$modal-dialog-margin-y-sm-up*2);
}

.modal-dialog-centered{
min-height:subtract(100%,$modal-dialog-margin-y-sm-up*2);
}

.modal-content{
@includebox-shadow($modal-content-box-shadow-sm-up);
}

.modal-sm{max-width:$modal-sm;}
}

@includemedia-breakpoint -up(lg){
.modal-lg,
.modal-xl{
max-width:$modal-lg;
}
}

@includemedia-breakpoint -up(xl){
.modal-xl{max-width:$modal-xl;}
}

//scss-docs-startmodal-fullscreen-loop
@each$breakpoint inmap-keys($grid-breakpoint s){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);
$postfix:if($infix!="",$infix+"-down","");

@includemedia-breakpoint -down($breakpoint ){
.modal-fullscreen#{$postfix}{
width:100vw;
max-width:none;
height:100%;
margin:0;

.modal-content{
height:100%;
border:0;
@includeborder-radius(0);
}

.modal-header{
@includeborder-radius(0);
}

.modal-body{
overflow-y:auto;
}

.modal-footer{
@includeborder-radius(0);
}
}
}
}
//scss-docs-endmodal-fullscreen-loop
