//Navbar
//
//Provideastaticnavbarfromwhichweexpandtocreatefull-width,fixed,and
//othernavbarvar iations.

.navbar{
position:relative;
display:flex;
flex-wrap:wrap;//allowustodothelinebreakforcollapsingcontent
align-items:center;
justify-content:space-between;//spaceoutbrandfromlogo
padding-top:$navbar-padding-y;
padding-right:$navbar-padding-x;//default:null
padding-bottom:$navbar-padding-y;
padding-left:$navbar-padding-x;//default:null
@includegradient-bg();

//Becauseflexpropertiesaren'tinherited,weneedtoredeclarethesefirst
//fewpropertiessothatcontentnestedwithinbehaveproperly.
//The`flex-wrap`propertyisinheritedtosimplifytheexpandednavbars
%container-flex-properties{
display:flex;
flex-wrap:inherit;
align-items:center;
justify-content:space-between;
}

>.container,
>.container-fluid{
@extend%container-flex-properties;
}

@each$breakpoint ,$container-max-widthin$container-max-widths{
>.container#{breakpoint -infix($breakpoint ,$container-max-widths)}{
@extend%container-flex-properties;
}
}
}


//Navbarbrand
//
//Usedforbrand,project,orsitenames.

.navbar-brand{
padding-top:$navbar-brand-padding-y;
padding-bottom:$navbar-brand-padding-y;
margin-right:$navbar-brand-margin-end;
@includefont-size($navbar-brand-font-size);
text-decoration:if($link-decoration==none,null,none);
white-space:nowrap;

&:hover,
&:focus{
text-decoration:if($link-hover-decoration==underline,none,null);
}
}


//Navbarnav
//
//Customnavbarnavigation(doesn'trequire`.nav`,butdoesmakeuseof`.nav-link`).

.navbar-nav{
display:flex;
flex-direction:column;//cannotuse`inherit`togetthe`.navbar`svalue
padding-left:0;
margin-bottom:0;
list-style:none;

.nav-link{
padding-right:0;
padding-left:0;
}

.dropdown-menu{
position:static;
}
}


//Navbartext
//
//

.navbar-text{
padding-top:$nav-link-padding-y;
padding-bottom:$nav-link-padding-y;
}


//Responsivenavbar
//
//Customstylesforresponsivecollapsingandtogglingofnavbarcontents.
//PoweredbythecollapseBootstrapJavaScriptplugin.

//Whencollapsed,preventthetoggleablenavbarcontentsfromappearingin
//thedefaultflexboxroworientation.Requirestheuseof`flex-wrap:wrap`
//onthe`.navbar`parent.
.navbar-collapse{
flex-basis:100%;
flex-grow:1;
//Foralwaysexpandedorextrafullnavbars,ensurecontentalignsitself
//properlyvertically.Canbeeasilyoverriddenwithflexutilities.
align-items:center;
}

//Buttonfortogglingthenavbarwheninitscollapsedstate
.navbar-toggler{
padding:$navbar-toggler-padding-y$navbar-toggler-padding-x;
@includefont-size($navbar-toggler-font-size);
line-height:1;
background-color:transparent;//removedefaultbuttonstyle
border:$border-widthsolidtransparent;//removedefaultbuttonstyle
@includeborder-radius($navbar-toggler-border-radius);
@includetransition($navbar-toggler-transition);

&:hover{
text-decoration:none;
}

&:focus{
text-decoration:none;
outline:0;
box-shadow:000$navbar-toggler-focus-width;
}
}

//Keepasaseparateelementsofolkscaneasilyoverrideitwithanothericon
//orimagefileasneeded.
.navbar-toggler-icon{
display:inline-block;
width:1.5em;
height:1.5em;
vertical-align:middle;
background-repeat:no-repeat;
background-position:center;
background-size:100%;
}

.navbar-nav-scroll{
max-height:var (--#{$var iable-prefix}scroll-height,75vh);
overflow-y:auto;
}

//scss-docs-startnavbar-expand-loop
//Generateseriesof`.navbar-expand-*`responsiveclass esforconfiguring
//whereyournavbarcollapses.
.navbar-expand{
@each$breakpoint inmap-keys($grid-breakpoint s){
$next:breakpoint -next($breakpoint ,$grid-breakpoint s);
$infix:breakpoint -infix($next,$grid-breakpoint s);

//stylelint -disable-next-linescss/selector-no-union-class -name
&#{$infix}{
@includemedia-breakpoint -up($next){
flex-wrap:nowrap;
justify-content:flex-start;

.navbar-nav{
flex-direction:row;

.dropdown-menu{
position:absolute;
}

.nav-link{
padding-right:$navbar-nav-link-padding-x;
padding-left:$navbar-nav-link-padding-x;
}
}

.navbar-nav-scroll{
overflow:visible;
}

.navbar-collapse{
display:flex!important;//stylelint -disable-linedeclaration-no-important
flex-basis:auto;
}

.navbar-toggler{
display:none;
}

.offcanvas-header{
display:none;
}

.offcanvas{
position:inherit;
bottom:0;
z-index:1000;
flex-grow:1;
visibility:visible!important;//stylelint -disable-linedeclaration-no-important
background-color:transparent;
border-right:0;
border-left:0;
@includetransition(none);
transform:none;
}
.offcanvas-top,
.offcanvas-bottom{
height:auto;
border-top:0;
border-bottom:0;
}

.offcanvas-body{
display:flex;
flex-grow:0;
padding:0;
overflow-y:visible;
}
}
}
}
}
//scss-docs-endnavbar-expand-loop

//Navbarthemes
//
//Stylesforswitchingbetweennavbarswithlightordarkbackground.

//Darklinksagainstalightbackground
.navbar-light{
.navbar-brand{
color:$navbar-light-brand-color;

&:hover,
&:focus{
color:$navbar-light-brand-hover-color;
}
}

.navbar-nav{
.nav-link{
color:$navbar-light-color;

&:hover,
&:focus{
color:$navbar-light-hover-color;
}

&.disabled{
color:$navbar-light-disabled-color;
}
}

.show>.nav-link,
.nav-link.active{
color:$navbar-light-active-color;
}
}

.navbar-toggler{
color:$navbar-light-color;
border-color:$navbar-light-toggler-border-color;
}

.navbar-toggler-icon{
background-image:escape-svg($navbar-light-toggler-icon-bg);
}

.navbar-text{
color:$navbar-light-color;

a,
a:hover,
a:focus{
color:$navbar-light-active-color;
}
}
}

//Whitelinksagainstadarkbackground
.navbar-dark{
.navbar-brand{
color:$navbar-dark-brand-color;

&:hover,
&:focus{
color:$navbar-dark-brand-hover-color;
}
}

.navbar-nav{
.nav-link{
color:$navbar-dark-color;

&:hover,
&:focus{
color:$navbar-dark-hover-color;
}

&.disabled{
color:$navbar-dark-disabled-color;
}
}

.show>.nav-link,
.nav-link.active{
color:$navbar-dark-active-color;
}
}

.navbar-toggler{
color:$navbar-dark-color;
border-color:$navbar-dark-toggler-border-color;
}

.navbar-toggler-icon{
background-image:escape-svg($navbar-dark-toggler-icon-bg);
}

.navbar-text{
color:$navbar-dark-color;
a,
a:hover,
a:focus{
color:$navbar-dark-active-color;
}
}
}
