//Thedropdownwrapper(`<div>`)
.dropup,
.dropend,
.dropdown,
.dropstart{
position:relative;
}

.dropdown-toggle{
white-space:nowrap;

//Generatethecaretautomatically
@includecaret();
}

//Thedropdownmenu
.dropdown-menu{
position:absolute;
z-index:$zindex-dropdown;
display:none;//nonebydefault,butblockon"open"ofthemenu
min-width:$dropdown-min-width;
padding:$dropdown-padding-y$dropdown-padding-x;
margin:0;//Overridedefaultmarginoful
@includefont-size($dropdown-font-size);
color:$dropdown-color;
text-align:left;//Ensuresproperalignmentifparenthasitchanged(e.g.,modalfooter)
list-style:none;
background-color:$dropdown-bg;
background-clip:padding-box;
border:$dropdown-border-widthsolid$dropdown-border-color;
@includeborder-radius($dropdown-border-radius);
@includebox-shadow($dropdown-box-shadow);

&[data-bs-popper]{
top:100%;
left:0;
margin-top:$dropdown-spacer;
}
}

//scss-docs-startresponsive-breakpoint s
//Wedeliberatelyhardcodethe`bs-`prefixbecausewecheck
//thiscustompropertyinJStodeterminePopper'spositioning

@each$breakpoint inmap-keys($grid-breakpoint s){
@includemedia-breakpoint -up($breakpoint ){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

.dropdown-menu#{$infix}-start{
--bs-position:start;

&[data-bs-popper]{
right:auto;
left:0;
}
}

.dropdown-menu#{$infix}-end{
--bs-position:end;

&[data-bs-popper]{
right:0;
left:auto;
}
}
}
}
//scss-docs-endresponsive-breakpoint s

//Allowfordropdownstogobottomup(aka,dropup-menu)
//Justadd.dropupafterthestandard.dropdownclass andyou'reset.
.dropup{
.dropdown-menu[data-bs-popper]{
top:auto;
bottom:100%;
margin-top:0;
margin-bottom:$dropdown-spacer;
}

.dropdown-toggle{
@includecaret(up);
}
}

.dropend{
.dropdown-menu[data-bs-popper]{
top:0;
right:auto;
left:100%;
margin-top:0;
margin-left:$dropdown-spacer;
}

.dropdown-toggle{
@includecaret(end);
&::after{
vertical-align:0;
}
}
}

.dropstart{
.dropdown-menu[data-bs-popper]{
top:0;
right:100%;
left:auto;
margin-top:0;
margin-right:$dropdown-spacer;
}

.dropdown-toggle{
@includecaret(start);
&::before{
vertical-align:0;
}
}
}


//Dividers(basicallyan`<hr>`)withint hedropdown
.dropdown-divider{
height:0;
margin:$dropdown-divider-margin-y0;
overflow:hidden;
border-top:1pxsolid$dropdown-divider-bg;
}

//Links,buttons,andmorewithint hedropdownmenu
//
//`<button>`-specificstylesaredenotedwith`//For<button>s`
.dropdown-item{
display:block;
width:100%;//For`<button>`s
padding:$dropdown-item-padding-y$dropdown-item-padding-x;
clear:both;
font-weight:$font-weight-normal;
color:$dropdown-link-color;
text-align:inherit;//For`<button>`s
text-decoration:if($link-decoration==none,null,none);
white-space:nowrap;//preventlinksfromrandomlybreakingontonewlines
background-color:transparent;//For`<button>`s
border:0;//For`<button>`s

//Preventdropdownoverflowifthere'snopadding
//Seehttps://github.com/twbs/bootstrap/pull/27703
@if$dropdown-padding-y==0{
&:first-child{
@includeborder-top-radius($dropdown-inner-border-radius);
}

&:last-child{
@includeborder-bottom-radius($dropdown-inner-border-radius);
}
}

&:hover,
&:focus{
color:$dropdown-link-hover-color;
text-decoration:if($link-hover-decoration==underline,none,null);
@includegradient-bg($dropdown-link-hover-bg);
}

&.active,
&:active{
color:$dropdown-link-active-color;
text-decoration:none;
@includegradient-bg($dropdown-link-active-bg);
}

&.disabled,
&:disabled{
color:$dropdown-link-disabled-color;
point er-events:none;
background-color:transparent;
//RemoveCSSgradientsifthey'reenabled
background-image:if($enable-gradients,none,null);
}
}

.dropdown-menu.show{
display:block;
}

//Dropdownsectionheaders
.dropdown-header{
display:block;
padding:$dropdown-header-padding;
margin-bottom:0;//forusewithheadingelements
@includefont-size($font-size-sm);
color:$dropdown-header-color;
white-space:nowrap;//aswith>li>a
}

//Dropdowntext
.dropdown-item-text{
display:block;
padding:$dropdown-item-padding-y$dropdown-item-padding-x;
color:$dropdown-link-color;
}

//Darkdropdowns
.dropdown-menu-dark{
color:$dropdown-dark-color;
background-color:$dropdown-dark-bg;
border-color:$dropdown-dark-border-color;
@includebox-shadow($dropdown-dark-box-shadow);

.dropdown-item{
color:$dropdown-dark-link-color;

&:hover,
&:focus{
color:$dropdown-dark-link-hover-color;
@includegradient-bg($dropdown-dark-link-hover-bg);
}

&.active,
&:active{
color:$dropdown-dark-link-active-color;
@includegradient-bg($dropdown-dark-link-active-bg);
}

&.disabled,
&:disabled{
color:$dropdown-dark-link-disabled-color;
}
}

.dropdown-divider{
border-color:$dropdown-dark-divider-bg;
}

.dropdown-item-text{
color:$dropdown-dark-link-color;
}

.dropdown-header{
color:$dropdown-dark-header-color;
}
}
