.ratio{
position:relative;
width:100%;

&::before{
display:block;
padding-top:var (--#{$var iable-prefix}aspect-ratio);
content:"";
}

>*{
position:absolute;
top:0;
left:0;
width:100%;
height:100%;
}
}

@each$key,$ratioin$aspect-ratios{
.ratio-#{$key}{
--#{$var iable-prefix}aspect-ratio:#{$ratio};
}
}
