//Responsiveimages(ensureimagesdon'tscalebeyondtheirparents)
//
//Thisispurposefullyopt-inviaanexplicitclass ratherthanbeingthedefaultforall`<img>`s.
//Wepreviouslytriedthe"imagesareresponsivebydefault"approachinBootstrapv2,
//andabandoneditinBootstrapv3becauseitbreakslotsofthird-partywidgets(includingGoogleMaps)
//whichweren'texpectingtheimageswithint hemselvestobeinvoluntarilyresized.
//Seealsohttps://github.com/twbs/bootstrap/issues/18178
.img-fluid{
@includeimg-fluid();
}


//Imagethumbnails
.img-thumbnail{
padding:$thumbnail-padding;
background-color:$thumbnail-bg;
border:$thumbnail-border-widthsolid$thumbnail-border-color;
@includeborder-radius($thumbnail-border-radius);
@includebox-shadow($thumbnail-box-shadow);

//Keepthematmost100%wide
@includeimg-fluid();
}

//
//Figures
//

.figure{
//Ensuresthecaption'stextalignswiththeimage.
display:inline-block;
}

.figure-img{
margin-bottom:$spacer*.5;
line-height:1;
}

.figure-caption{
@includefont-size($figure-caption-font-size);
color:$figure-caption-color;
}
