//Thismixinusesan`if()`techniquetobecompatiblewithDartSass
//Seehttps://github.com/sass/sass/issues/1873#issuecomment-152293725formoredetails

//scss-docs-startform-validation-mixins
@mixinform-validation-state-selector($state){
@if($state=="valid"or$state=="invalid"){
.was-validated#{if(&,"&","")}:#{$state},
#{if(&,"&","")}.is-#{$state}{
@content;
}
}@else{
#{if(&,"&","")}.is-#{$state}{
@content;
}
}
}

@mixinform-validation-state(
$state,
$color,
$icon,
$tooltip-color:color-contrast($color),
$tooltip-bg-color:rgba($color,$form-feedback-tooltip-opacity),
$focus-box-shadow:00$input-btn-focus-blur$input-focus-widthrgba($color,$input-btn-focus-color-opacity)
){
.#{$state}-feedback{
display:none;
width:100%;
margin-top:$form-feedback-margin-top;
@includefont-size($form-feedback-font-size);
font-style:$form-feedback-font-style;
color:$color;
}

.#{$state}-tooltip{
position:absolute;
top:100%;
z-index:5;
display:none;
max-width:100%;//Containt oparentwhenpossible
padding:$form-feedback-tooltip-padding-y$form-feedback-tooltip-padding-x;
margin-top:.1rem;
@includefont-size($form-feedback-tooltip-font-size);
line-height:$form-feedback-tooltip-line-height;
color:$tooltip-color;
background-color:$tooltip-bg-color;
@includeborder-radius($form-feedback-tooltip-border-radius);
}

@includeform-validation-state-selector($state){
~.#{$state}-feedback,
~.#{$state}-tooltip{
display:block;
}
}

.form-control{
@includeform-validation-state-selector($state){
border-color:$color;

@if$enable-validation-icons{
padding-right:$input-height-inner;
background-image:escape-svg($icon);
background-repeat:no-repeat;
background-position:right$input-height-inner-quartercenter;
background-size:$input-height-inner-half$input-height-inner-half;
}

&:focus{
border-color:$color;
box-shadow:$focus-box-shadow;
}
}
}

//stylelint -disable-next-lineselector-no-qualifying-type
textarea.form-control{
@includeform-validation-state-selector($state){
@if$enable-validation-icons{
padding-right:$input-height-inner;
background-position:top$input-height-inner-quarterright$input-height-inner-quarter;
}
}
}

.form-select{
@includeform-validation-state-selector($state){
border-color:$color;

@if$enable-validation-icons{
&:not([multiple]):not([size]),
&:not([multiple])[size="1"]{
padding-right:$form-select-feedback-icon-padding-end;
background-image:escape-svg($form-select-indicator),escape-svg($icon);
background-position:$form-select-bg-position,$form-select-feedback-icon-position;
background-size:$form-select-bg-size,$form-select-feedback-icon-size;
}
}

&:focus{
border-color:$color;
box-shadow:$focus-box-shadow;
}
}
}

.form-check-input{
@includeform-validation-state-selector($state){
border-color:$color;

&:checked{
background-color:$color;
}

&:focus{
box-shadow:$focus-box-shadow;
}

~.form-check-label{
color:$color;
}
}
}
.form-check-inline.form-check-input{
~.#{$state}-feedback{
margin-left:.5em;
}
}

.input-group.form-control,
.input-group.form-select{
@includeform-validation-state-selector($state){
@if$state=="valid"{
z-index:1;
}@elseif$state=="invalid"{
z-index:2;
}
&:focus{
z-index:3;
}
}
}
}
//scss-docs-endform-validation-mixins
