//Baseclass 
.tooltip{
position:absolute;
z-index:$zindex-tooltip;
display:block;
margin:$tooltip-margin;
//Ourparentelementcanbearbitrarysincetooltipsarebydefaultinsertedasasiblingoftheirtargetelement.
//Soresetourfontandtextpropertiestoavoidinheritingweirdvalues.
@includereset-text();
@includefont-size($tooltip-font-size);
//Allowbreakingverylongwordssotheydon'toverflowthetooltip'sbounds
word-wrap:break-word;
opacity:0;

&.show{opacity:$tooltip-opacity;}

.tooltip-arrow{
position:absolute;
display:block;
width:$tooltip-arrow-width;
height:$tooltip-arrow-height;

&::before{
position:absolute;
content:"";
border-color:transparent;
border-style:solid;
}
}
}

.bs-tooltip-top{
padding:$tooltip-arrow-height0;

.tooltip-arrow{
bottom:0;

&::before{
top:-1px;
border-width:$tooltip-arrow-height($tooltip-arrow-width*.5)0;
border-top-color:$tooltip-arrow-color;
}
}
}

.bs-tooltip-end{
padding:0$tooltip-arrow-height;

.tooltip-arrow{
left:0;
width:$tooltip-arrow-height;
height:$tooltip-arrow-width;

&::before{
right:-1px;
border-width:($tooltip-arrow-width*.5)$tooltip-arrow-height($tooltip-arrow-width*.5)0;
border-right-color:$tooltip-arrow-color;
}
}
}

.bs-tooltip-bottom{
padding:$tooltip-arrow-height0;

.tooltip-arrow{
top:0;

&::before{
bottom:-1px;
border-width:0($tooltip-arrow-width*.5)$tooltip-arrow-height;
border-bottom-color:$tooltip-arrow-color;
}
}
}

.bs-tooltip-start{
padding:0$tooltip-arrow-height;

.tooltip-arrow{
right:0;
width:$tooltip-arrow-height;
height:$tooltip-arrow-width;

&::before{
left:-1px;
border-width:($tooltip-arrow-width*.5)0($tooltip-arrow-width*.5)$tooltip-arrow-height;
border-left-color:$tooltip-arrow-color;
}
}
}

.bs-tooltip-auto{
&[data-popper-placement^="top"]{
@extend.bs-tooltip-top;
}
&[data-popper-placement^="right"]{
@extend.bs-tooltip-end;
}
&[data-popper-placement^="bottom"]{
@extend.bs-tooltip-bottom;
}
&[data-popper-placement^="left"]{
@extend.bs-tooltip-start;
}
}

//Wrapperforthetooltipcontent
.tooltip-inner{
max-width:$tooltip-max-width;
padding:$tooltip-padding-y$tooltip-padding-x;
color:$tooltip-color;
text-align:center;
background-color:$tooltip-bg;
@includeborder-radius($tooltip-border-radius);
}
