//Disableanimationiftransitionsaredisabled

//scss-docs-startprogress-keyframes
@if$enable-transitions{
@keyframesprogress-bar-stripes{
0%{background-position-x:$progress-height;}
}
}
//scss-docs-endprogress-keyframes

.progress{
display:flex;
height:$progress-height;
overflow:hidden;//forceroundedcornersbycroppingit
@includefont-size($progress-font-size);
background-color:$progress-bg;
@includeborder-radius($progress-border-radius);
@includebox-shadow($progress-box-shadow);
}

.progress-bar{
display:flex;
flex-direction:column;
justify-content:center;
overflow:hidden;
color:$progress-bar-color;
text-align:center;
white-space:nowrap;
background-color:$progress-bar-bg;
@includetransition($progress-bar-transition);
}

.progress-bar-striped{
@includegradient-striped();
background-size:$progress-height$progress-height;
}

@if$enable-transitions{
.progress-bar-animated{
animation:$progress-bar-animation-timingprogress-bar-stripes;

@if$enable-reduced-motion{
@media(prefers-reduced-motion:reduce){
animation:none;
}
}
}
}
