//
//Rotatingborder
//

//scss-docs-startspinner-border-keyframes
@keyframesspinner-border{
to{transform:rotate(360deg)#{"/*rtl:ignore*/"};}
}
//scss-docs-endspinner-border-keyframes

.spinner-border{
display:inline-block;
width:$spinner-width;
height:$spinner-height;
vertical-align:$spinner-vertical-align;
border:$spinner-border-widthsolidcurrentColor;
border-right-color:transparent;
//stylelint -disable-next-lineproperty-disallowed-list
border-radius:50%;
animation:$spinner-animation-speedlinearinfinitespinner-border;
}

.spinner-border-sm{
width:$spinner-width-sm;
height:$spinner-height-sm;
border-width:$spinner-border-width-sm;
}

//
//Growingcircle
//

//scss-docs-startspinner-grow-keyframes
@keyframesspinner-grow{
0%{
transform:scale(0);
}
50%{
opacity:1;
transform:none;
}
}
//scss-docs-endspinner-grow-keyframes

.spinner-grow{
display:inline-block;
width:$spinner-width;
height:$spinner-height;
vertical-align:$spinner-vertical-align;
background-color:currentColor;
//stylelint -disable-next-lineproperty-disallowed-list
border-radius:50%;
opacity:0;
animation:$spinner-animation-speedlinearinfinitespinner-grow;
}

.spinner-grow-sm{
width:$spinner-width-sm;
height:$spinner-height-sm;
}

@if$enable-reduced-motion{
@media(prefers-reduced-motion:reduce){
.spinner-border,
.spinner-grow{
animation-duration:$spinner-animation-speed*2;
}
}
}
