//Loopovereachbreakpoint 
@each$breakpoint inmap-keys($grid-breakpoint s){

//Generatemediaqueryifneeded
@includemedia-breakpoint -up($breakpoint ){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

//Loopovereachutilityproperty
@each$key,$utilityin$utilities{
//Theutilitycanbedisabledwith`false`,thuscheckiftheutilityisamapfirst
//Onlyproceedifresponsivemediaqueriesareenabledorifit'sthebasemediaquery
@iftype-of($utility)=="map"and(map-get($utility,responsive)or$infix==""){
@includegenerate-utility($utility,$infix);
}
}
}
}

//RFSrescaling
@media(min-width:$rfs-mq-value){
@each$breakpoint inmap-keys($grid-breakpoint s){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

@if(map-get($grid-breakpoint s,$breakpoint )<$rfs-breakpoint ){
//Loopovereachutilityproperty
@each$key,$utilityin$utilities{
//Theutilitycanbedisabledwith`false`,thuscheckiftheutilityisamapfirst
//Onlyproceedifresponsivemediaqueriesareenabledorifit'sthebasemediaquery
@iftype-of($utility)=="map"andmap-get($utility,rfs)and(map-get($utility,responsive)or$infix==""){
@includegenerate-utility($utility,$infix,true);
}
}
}
}
}


//Print utilities
@mediaprint {
@each$key,$utilityin$utilities{
//Theutilitycanbedisabledwith`false`,thuscheckiftheutilityisamapfirst
//Thencheckiftheutilityneedsprint styles
@iftype-of($utility)=="map"andmap-get($utility,print )==true{
@includegenerate-utility($utility,"-print ");
}
}
}
