//Pagination

//scss-docs-startpagination-mixin
@mixinpagination-size($padding-y,$padding-x,$font-size,$border-radius){
.page-link{
padding:$padding-y$padding-x;
@includefont-size($font-size);
}

.page-item{
@if$pagination-margin-start==(-$pagination-border-width){
&:first-child{
.page-link{
@includeborder-start-radius($border-radius);
}
}

&:last-child{
.page-link{
@includeborder-end-radius($border-radius);
}
}
}@else{
//Addborder-radiustoallpageLinksincasetheyhaveleftmargin
.page-link{
@includeborder-radius($border-radius);
}
}
}
}
//scss-docs-endpagination-mixin
