//Baseclass 
//
//Kickstartanynavigationcomponentwithasetofstyleresets.Workswith
//`<nav>`s,`<ul>`sor`<ol>`s.

.nav{
display:flex;
flex-wrap:wrap;
padding-left:0;
margin-bottom:0;
list-style:none;
}

.nav-link{
display:block;
padding:$nav-link-padding-y$nav-link-padding-x;
@includefont-size($nav-link-font-size);
font-weight:$nav-link-font-weight;
color:$nav-link-color;
text-decoration:if($link-decoration==none,null,none);
@includetransition($nav-link-transition);

&:hover,
&:focus{
color:$nav-link-hover-color;
text-decoration:if($link-hover-decoration==underline,none,null);
}

//Disabledstatelightenstext
&.disabled{
color:$nav-link-disabled-color;
point er-events:none;
cursor:default;
}
}

//
//Tabs
//

.nav-tabs{
border-bottom:$nav-tabs-border-widthsolid$nav-tabs-border-color;

.nav-link{
margin-bottom:-$nav-tabs-border-width;
background:none;
border:$nav-tabs-border-widthsolidtransparent;
@includeborder-top-radius($nav-tabs-border-radius);

&:hover,
&:focus{
border-color:$nav-tabs-link-hover-border-color;
//Preventsactive.nav-linktaboverlappingfocusoutlineofprevious/next.nav-link
isolation:isolate;
}

&.disabled{
color:$nav-link-disabled-color;
background-color:transparent;
border-color:transparent;
}
}

.nav-link.active,
.nav-item.show.nav-link{
color:$nav-tabs-link-active-color;
background-color:$nav-tabs-link-active-bg;
border-color:$nav-tabs-link-active-border-color;
}

.dropdown-menu{
//Makedropdownborderoverlaptabborder
margin-top:-$nav-tabs-border-width;
//Removethetoproundedcornersheresincethereisahardedgeabovethemenu
@includeborder-top-radius(0);
}
}


//
//Pills
//

.nav-pills{
.nav-link{
background:none;
border:0;
@includeborder-radius($nav-pills-border-radius);
}

.nav-link.active,
.show>.nav-link{
color:$nav-pills-link-active-color;
@includegradient-bg($nav-pills-link-active-bg);
}
}


//
//Justifiedvar iants
//

.nav-fill{
>.nav-link,
.nav-item{
flex:11auto;
text-align:center;
}
}

.nav-justified{
>.nav-link,
.nav-item{
flex-basis:0;
flex-grow:1;
text-align:center;
}
}

.nav-fill,
.nav-justified{
.nav-item.nav-link{
width:100%;//Makesurebuttonwillgrow
}
}


//Tabbabletabs
//
//Hidetabbablepanestostart,showthemwhen`.active`

.tab-content{
>.tab-pane{
display:none;
}
>.active{
display:block;
}
}
