//Buttonvar iants
//
//Easilypumpoutdefaultstyles,aswellas:hover,:focus,:active,
//anddisabledoptionsforallbuttons

//scss-docs-startbtn-var iant-mixin
@mixinbutton-var iant(
$background,
$border,
$color:color-contrast($background),
$hover-background:if($color==$color-contrast-light,shade-color($background,$btn-hover-bg-shade-amount),tint -color($background,$btn-hover-bg-tint -amount)),
$hover-border:if($color==$color-contrast-light,shade-color($border,$btn-hover-border-shade-amount),tint -color($border,$btn-hover-border-tint -amount)),
$hover-color:color-contrast($hover-background),
$active-background:if($color==$color-contrast-light,shade-color($background,$btn-active-bg-shade-amount),tint -color($background,$btn-active-bg-tint -amount)),
$active-border:if($color==$color-contrast-light,shade-color($border,$btn-active-border-shade-amount),tint -color($border,$btn-active-border-tint -amount)),
$active-color:color-contrast($active-background),
$disabled-background:$background,
$disabled-border:$border,
$disabled-color:color-contrast($disabled-background)
){
color:$color;
@includegradient-bg($background);
border-color:$border;
@includebox-shadow($btn-box-shadow);

&:hover{
color:$hover-color;
@includegradient-bg($hover-background);
border-color:$hover-border;
}

.btn-check:focus+&,
&:focus{
color:$hover-color;
@includegradient-bg($hover-background);
border-color:$hover-border;
@if$enable-shadows{
@includebox-shadow($btn-box-shadow,000$btn-focus-widthrgba(mix($color,$border,15%),.5));
}@else{
//Avoidusing mixinsowecanpasscustomfocusshadowproperly
box-shadow:000$btn-focus-widthrgba(mix($color,$border,15%),.5);
}
}

.btn-check:checked+&,
.btn-check:active+&,
&:active,
&.active,
.show>&.dropdown-toggle{
color:$active-color;
background-color:$active-background;
//RemoveCSSgradientsifthey'reenabled
background-image:if($enable-gradients,none,null);
border-color:$active-border;

&:focus{
@if$enable-shadows{
@includebox-shadow($btn-active-box-shadow,000$btn-focus-widthrgba(mix($color,$border,15%),.5));
}@else{
//Avoidusing mixinsowecanpasscustomfocusshadowproperly
box-shadow:000$btn-focus-widthrgba(mix($color,$border,15%),.5);
}
}
}

&:disabled,
&.disabled{
color:$disabled-color;
background-color:$disabled-background;
//RemoveCSSgradientsifthey'reenabled
background-image:if($enable-gradients,none,null);
border-color:$disabled-border;
}
}
//scss-docs-endbtn-var iant-mixin

//scss-docs-startbtn-outline-var iant-mixin
@mixinbutton-outline-var iant(
$color,
$color-hover:color-contrast($color),
$active-background:$color,
$active-border:$color,
$active-color:color-contrast($active-background)
){
color:$color;
border-color:$color;

&:hover{
color:$color-hover;
background-color:$active-background;
border-color:$active-border;
}

.btn-check:focus+&,
&:focus{
box-shadow:000$btn-focus-widthrgba($color,.5);
}

.btn-check:checked+&,
.btn-check:active+&,
&:active,
&.active,
&.dropdown-toggle.show{
color:$active-color;
background-color:$active-background;
border-color:$active-border;

&:focus{
@if$enable-shadows{
@includebox-shadow($btn-active-box-shadow,000$btn-focus-widthrgba($color,.5));
}@else{
//Avoidusing mixinsowecanpasscustomfocusshadowproperly
box-shadow:000$btn-focus-widthrgba($color,.5);
}
}
}

&:disabled,
&.disabled{
color:$color;
background-color:transparent;
}
}
//scss-docs-endbtn-outline-var iant-mixin

//scss-docs-startbtn-size-mixin
@mixinbutton-size($padding-y,$padding-x,$font-size,$border-radius){
padding:$padding-y$padding-x;
@includefont-size($font-size);
//Manuallydeclaretoprovideanoverridetothebrowserdefault
@includeborder-radius($border-radius,0);
}
//scss-docs-endbtn-size-mixin
