//Shorthand

.fixed-top{
position:fixed;
top:0;
right:0;
left:0;
z-index:$zindex-fixed;
}

.fixed-bottom{
position:fixed;
right:0;
bottom:0;
left:0;
z-index:$zindex-fixed;
}

//Responsivestickytop
@each$breakpoint inmap-keys($grid-breakpoint s){
@includemedia-breakpoint -up($breakpoint ){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

.sticky#{$infix}-top{
position:sticky;
top:0;
z-index:$zindex-sticky;
}
}
}
