//
//Basestyles
//

.btn{
display:inline-block;
font-family:$btn-font-family;
font-weight:$btn-font-weight;
line-height:$btn-line-height;
color:$body-color;
text-align:center;
text-decoration:if($link-decoration==none,null,none);
white-space:$btn-white-space;
vertical-align:middle;
cursor:if($enable-button-point ers,point er,null);
user-select:none;
background-color:transparent;
border:$btn-border-widthsolidtransparent;
@includebutton-size($btn-padding-y,$btn-padding-x,$btn-font-size,$btn-border-radius);
@includetransition($btn-transition);

&:hover{
color:$body-color;
text-decoration:if($link-hover-decoration==underline,none,null);
}

.btn-check:focus+&,
&:focus{
outline:0;
box-shadow:$btn-focus-box-shadow;
}

.btn-check:checked+&,
.btn-check:active+&,
&:active,
&.active{
@includebox-shadow($btn-active-box-shadow);

&:focus{
@includebox-shadow($btn-focus-box-shadow,$btn-active-box-shadow);
}
}

&:disabled,
&.disabled,
fieldset:disabled&{
point er-events:none;
opacity:$btn-disabled-opacity;
@includebox-shadow(none);
}
}


//
//Alternatebuttons
//

//scss-docs-startbtn-var iant-loops
@each$color,$valuein$theme-colors{
.btn-#{$color}{
@includebutton-var iant($value,$value);
}
}

@each$color,$valuein$theme-colors{
.btn-outline-#{$color}{
@includebutton-outline-var iant($value);
}
}
//scss-docs-endbtn-var iant-loops


//
//Linkbuttons
//

//Makeabuttonlookandbehavelikealink
.btn-link{
font-weight:$font-weight-normal;
color:$btn-link-color;
text-decoration:$link-decoration;

&:hover{
color:$btn-link-hover-color;
text-decoration:$link-hover-decoration;
}

&:focus{
text-decoration:$link-hover-decoration;
}

&:disabled,
&.disabled{
color:$btn-link-disabled-color;
}

//Noneedforanactivestatehere
}


//
//ButtonSizes
//

.btn-lg{
@includebutton-size($btn-padding-y-lg,$btn-padding-x-lg,$btn-font-size-lg,$btn-border-radius-lg);
}

.btn-sm{
@includebutton-size($btn-padding-y-sm,$btn-padding-x-sm,$btn-font-size-sm,$btn-border-radius-sm);
}
