.breadcrumb{
display:flex;
flex-wrap:wrap;
padding:$breadcrumb-padding-y$breadcrumb-padding-x;
margin-bottom:$breadcrumb-margin-bottom;
@includefont-size($breadcrumb-font-size);
list-style:none;
background-color:$breadcrumb-bg;
@includeborder-radius($breadcrumb-border-radius);
}

.breadcrumb-item{
//Theseparatorbetweenbreadcrumbs(bydefault,aforward-slash:"/")
+.breadcrumb-item{
padding-left:$breadcrumb-item-padding-x;

&::before{
float:left;//Suppressinlinespacingsandunderliningoftheseparator
padding-right:$breadcrumb-item-padding-x;
color:$breadcrumb-divider-color;
content:var (--#{$var iable-prefix}breadcrumb-divider,escape-svg($breadcrumb-divider))#{"/*rtl:"}var (--#{$var iable-prefix}breadcrumb-divider,escape-svg($breadcrumb-divider-flipped))#{"*/"};
}
}

&.active{
color:$breadcrumb-active-color;
}
}
