//Baseclass 
//
//Easilyusableon<ul>,<ol>,or<div>.

.list-group{
display:flex;
flex-direction:column;

//Noneedtosetlist-style:none;since.list-group-itemisblocklevel
padding-left:0;//resetpaddingbecauseulandol
margin-bottom:0;
@includeborder-radius($list-group-border-radius);
}

.list-group-numbered{
list-style-type:none;
counter-reset:section;

>li::before{
//Incrementsonlythisinstanceofthesectioncounter
content:counters(section,".")".";
counter-increment:section;
}
}


//int eractivelistitems
//
//Useanchororbuttonelementsinsteadof`li`sor`div`stocreateint eractive
//listitems.Includesanextra`.active`modifierclass forselecteditems.

.list-group-item-action{
width:100%;//For`<button>`s(anchorsbecome100%bydefaultthough)
color:$list-group-action-color;
text-align:inherit;//For`<button>`s(anchorsinherit)

//Hoverstate
&:hover,
&:focus{
z-index:1;//Placehover/focusitemsabovetheirsiblingsforproperborderstyling
color:$list-group-action-hover-color;
text-decoration:none;
background-color:$list-group-hover-bg;
}

&:active{
color:$list-group-action-active-color;
background-color:$list-group-action-active-bg;
}
}


//Individuallistitems
//
//Useon`li`sor`div`swithint he`.list-group`parent.

.list-group-item{
position:relative;
display:block;
padding:$list-group-item-padding-y$list-group-item-padding-x;
color:$list-group-color;
text-decoration:if($link-decoration==none,null,none);
background-color:$list-group-bg;
border:$list-group-border-widthsolid$list-group-border-color;

&:first-child{
@includeborder-top-radius(inherit);
}

&:last-child{
@includeborder-bottom-radius(inherit);
}

&.disabled,
&:disabled{
color:$list-group-disabled-color;
point er-events:none;
background-color:$list-group-disabled-bg;
}

//Includebothherefor`<a>`sand`<button>`s
&.active{
z-index:2;//Placeactiveitemsabovetheirsiblingsforproperborderstyling
color:$list-group-active-color;
background-color:$list-group-active-bg;
border-color:$list-group-active-border-color;
}

&+&{
border-top-width:0;

&.active{
margin-top:-$list-group-border-width;
border-top-width:$list-group-border-width;
}
}
}


//Horizontal
//
//Changethelayoutoflistgroupitemsfromvertical(default)tohorizontal.

@each$breakpoint inmap-keys($grid-breakpoint s){
@includemedia-breakpoint -up($breakpoint ){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

.list-group-horizontal#{$infix}{
flex-direction:row;

>.list-group-item{
&:first-child{
@includeborder-bottom-start-radius($list-group-border-radius);
@includeborder-top-end-radius(0);
}

&:last-child{
@includeborder-top-end-radius($list-group-border-radius);
@includeborder-bottom-start-radius(0);
}

&.active{
margin-top:0;
}

+.list-group-item{
border-top-width:$list-group-border-width;
border-left-width:0;

&.active{
margin-left:-$list-group-border-width;
border-left-width:$list-group-border-width;
}
}
}
}
}
}


//Flushlistitems
//
//Removebordersandborder-radiustokeeplistgroupitemsedge-to-edge.Most
//usefulwithinothercomponents(e.g.,cards).

.list-group-flush{
@includeborder-radius(0);

>.list-group-item{
border-width:00$list-group-border-width;

&:last-child{
border-bottom-width:0;
}
}
}


//scss-docs-startlist-group-modifiers
//Listgroupcontextualvar iants
//
//Addmodifierclass estochangetextandbackgroundcoloronindividualitems.
//Organizationally,thismustcomeafterthe`:hover`states.

@each$state,$valuein$theme-colors{
$list-group-var iant-bg:shift-color($value,$list-group-item-bg-scale);
$list-group-var iant-color:shift-color($value,$list-group-item-color-scale);
@if(contrast-ratio($list-group-var iant-bg,$list-group-var iant-color)<$min-contrast-ratio){
$list-group-var iant-color:mix($value,color-contrast($list-group-var iant-bg),abs($list-group-item-color-scale));
}

@includelist-group-item-var iant($state,$list-group-var iant-bg,$list-group-var iant-color);
}
//scss-docs-endlist-group-modifiers
