//Baseclass 
//
//Requiresoneofthecontextual,colormodifierclass esfor`color`and
//`background-color`.

.badge{
display:inline-block;
padding:$badge-padding-y$badge-padding-x;
@includefont-size($badge-font-size);
font-weight:$badge-font-weight;
line-height:1;
color:$badge-color;
text-align:center;
white-space:nowrap;
vertical-align:baseline;
@includeborder-radius($badge-border-radius);
@includegradient-bg();

//Emptybadgescollapseautomatically
&:empty{
display:none;
}
}

//Quickfixforbadgesinbuttons
.btn.badge{
position:relative;
top:-1px;
}
