.placeholder{
display:inline-block;
min-height:1em;
vertical-align:middle;
cursor:wait;
background-color:currentColor;
opacity:$placeholder-opacity-max;

&.btn::before{
display:inline-block;
content:"";
}
}

//Sizing
.placeholder-xs{
min-height:.6em;
}

.placeholder-sm{
min-height:.8em;
}

.placeholder-lg{
min-height:1.2em;
}

//Animation
.placeholder-glow{
.placeholder{
animation:placeholder-glow2sease-in-outinfinite;
}
}

@keyframesplaceholder-glow{
50%{
opacity:$placeholder-opacity-min;
}
}

.placeholder-wave{
mask-image:linear-gradient(130deg,$black55%,rgba(0,0,0,(1-$placeholder-opacity-min))75%,$black95%);
mask-size:200%100%;
animation:placeholder-wave2slinearinfinite;
}

@keyframesplaceholder-wave{
100%{
mask-position:-200%0%;
}
}
