//Range
//
//Stylerangeinputsthesameacrossbrowsers.Vendor-specificrulesforpseudo
//elementscannotbemixed.Assuch,therearenosharedstylesforfocusor
//activestatesonprefixedselectors.

.form-range{
width:100%;
height:add($form-range-thumb-height,$form-range-thumb-focus-box-shadow-width*2);
padding:0;//Needtoresetpadding
background-color:transparent;
appearance:none;

&:focus{
outline:0;

//Pseudo-elementsmustbesplitacrossmultiplerulesetstohaveaneffect.
//Nobox-shadow()mixinforfocusaccessibility.
&::-webkit-slider-thumb{box-shadow:$form-range-thumb-focus-box-shadow;}
&::-moz-range-thumb{box-shadow:$form-range-thumb-focus-box-shadow;}
}

&::-moz-focus-outer{
border:0;
}

&::-webkit-slider-thumb{
width:$form-range-thumb-width;
height:$form-range-thumb-height;
margin-top:($form-range-track-height-$form-range-thumb-height)*.5;//Webkitspecific
@includegradient-bg($form-range-thumb-bg);
border:$form-range-thumb-border;
@includeborder-radius($form-range-thumb-border-radius);
@includebox-shadow($form-range-thumb-box-shadow);
@includetransition($form-range-thumb-transition);
appearance:none;

&:active{
@includegradient-bg($form-range-thumb-active-bg);
}
}

&::-webkit-slider-runnable-track{
width:$form-range-track-width;
height:$form-range-track-height;
color:transparent;//Why?
cursor:$form-range-track-cursor;
background-color:$form-range-track-bg;
border-color:transparent;
@includeborder-radius($form-range-track-border-radius);
@includebox-shadow($form-range-track-box-shadow);
}

&::-moz-range-thumb{
width:$form-range-thumb-width;
height:$form-range-thumb-height;
@includegradient-bg($form-range-thumb-bg);
border:$form-range-thumb-border;
@includeborder-radius($form-range-thumb-border-radius);
@includebox-shadow($form-range-thumb-box-shadow);
@includetransition($form-range-thumb-transition);
appearance:none;

&:active{
@includegradient-bg($form-range-thumb-active-bg);
}
}

&::-moz-range-track{
width:$form-range-track-width;
height:$form-range-track-height;
color:transparent;
cursor:$form-range-track-cursor;
background-color:$form-range-track-bg;
border-color:transparent;//Firefoxspecific?
@includeborder-radius($form-range-track-border-radius);
@includebox-shadow($form-range-track-box-shadow);
}

&:disabled{
point er-events:none;

&::-webkit-slider-thumb{
background-color:$form-range-thumb-disabled-bg;
}

&::-moz-range-thumb{
background-color:$form-range-thumb-disabled-bg;
}
}
}
