.offcanvas{
position:fixed;
bottom:0;
z-index:$zindex-offcanvas;
display:flex;
flex-direction:column;
max-width:100%;
color:$offcanvas-color;
visibility:hidden;
background-color:$offcanvas-bg-color;
background-clip:padding-box;
outline:0;
@includebox-shadow($offcanvas-box-shadow);
@includetransition(transform$offcanvas-transition-durationease-in-out);
}

.offcanvas-backdrop{
@includeoverlay-backdrop($zindex-offcanvas-backdrop,$offcanvas-backdrop-bg,$offcanvas-backdrop-opacity);
}

.offcanvas-header{
display:flex;
align-items:center;
justify-content:space-between;
padding:$offcanvas-padding-y$offcanvas-padding-x;

.btn-close{
padding:($offcanvas-padding-y*.5)($offcanvas-padding-x*.5);
margin-top:$offcanvas-padding-y*-.5;
margin-right:$offcanvas-padding-x*-.5;
margin-bottom:$offcanvas-padding-y*-.5;
}
}

.offcanvas-title{
margin-bottom:0;
line-height:$offcanvas-title-line-height;
}

.offcanvas-body{
flex-grow:1;
padding:$offcanvas-padding-y$offcanvas-padding-x;
overflow-y:auto;
}

.offcanvas-start{
top:0;
left:0;
width:$offcanvas-horizontal-width;
border-right:$offcanvas-border-widthsolid$offcanvas-border-color;
transform:translateX(-100%);
}

.offcanvas-end{
top:0;
right:0;
width:$offcanvas-horizontal-width;
border-left:$offcanvas-border-widthsolid$offcanvas-border-color;
transform:translateX(100%);
}

.offcanvas-top{
top:0;
right:0;
left:0;
height:$offcanvas-vertical-height;
max-height:100%;
border-bottom:$offcanvas-border-widthsolid$offcanvas-border-color;
transform:translateY(-100%);
}

.offcanvas-bottom{
right:0;
left:0;
height:$offcanvas-vertical-height;
max-height:100%;
border-top:$offcanvas-border-widthsolid$offcanvas-border-color;
transform:translateY(100%);
}

.offcanvas.show{
transform:none;
}
