.fade{
@includetransition($transition-fade);

&:not(.show){
opacity:0;
}
}

//scss-docs-startcollapse-class es
.collapse{
&:not(.show){
display:none;
}
}

.collapsing{
height:0;
overflow:hidden;
@includetransition($transition-collapse);

&.collapse-horizontal{
width:0;
height:auto;
@includetransition($transition-collapse-width);
}
}
//scss-docs-endcollapse-class es
