//Notesontheclass es:
//
//1..carousel.point er-eventshouldideallybepan-y(toallowforuserstoscrollvertically)
//evenwhentheirscrollactionstartedonacarousel,butforcompatibility(withFirefox)
//we'repreventingallactionsinstead
//2.The.carousel-item-startand.carousel-item-endisusedtoindicatewhere
//theactiveslideisheading.
//3..active.carousel-itemisthecurrentslide.
//4..active.carousel-item-startand.active.carousel-item-endisthecurrent
//slideinitsin-transitionstate.Onlyoneoftheseoccursatatime.
//5..carousel-item-next.carousel-item-startand.carousel-item-prev.carousel-item-end
//istheupcomingslideint ransition.

.carousel{
position:relative;
}

.carousel.point er-event{
touch-action:pan-y;
}

.carousel-inner{
position:relative;
width:100%;
overflow:hidden;
@includeclearfix();
}

.carousel-item{
position:relative;
display:none;
float:left;
width:100%;
margin-right:-100%;
backface-visibility:hidden;
@includetransition($carousel-transition);
}

.carousel-item.active,
.carousel-item-next,
.carousel-item-prev{
display:block;
}

/*rtl:begin:ignore*/
.carousel-item-next:not(.carousel-item-start),
.active.carousel-item-end{
transform:translateX(100%);
}

.carousel-item-prev:not(.carousel-item-end),
.active.carousel-item-start{
transform:translateX(-100%);
}

/*rtl:end:ignore*/


//
//Alternatetransitions
//

.carousel-fade{
.carousel-item{
opacity:0;
transition-property:opacity;
transform:none;
}

.carousel-item.active,
.carousel-item-next.carousel-item-start,
.carousel-item-prev.carousel-item-end{
z-index:1;
opacity:1;
}

.active.carousel-item-start,
.active.carousel-item-end{
z-index:0;
opacity:0;
@includetransition(opacity0s$carousel-transition-duration);
}
}


//
//Left/rightcontrolsfornav
//

.carousel-control-prev,
.carousel-control-next{
position:absolute;
top:0;
bottom:0;
z-index:1;
//Useflexforalignment(1-3)
display:flex;//1.allowflexstyles
align-items:center;//2.verticallycentercontents
justify-content:center;//3.horizontallycentercontents
width:$carousel-control-width;
padding:0;
color:$carousel-control-color;
text-align:center;
background:none;
border:0;
opacity:$carousel-control-opacity;
@includetransition($carousel-control-transition);

//Hover/focusstate
&:hover,
&:focus{
color:$carousel-control-color;
text-decoration:none;
outline:0;
opacity:$carousel-control-hover-opacity;
}
}
.carousel-control-prev{
left:0;
background-image:if($enable-gradients,linear-gradient(90deg,rgba($black,.25),rgba($black,.001)),null);
}
.carousel-control-next{
right:0;
background-image:if($enable-gradients,linear-gradient(270deg,rgba($black,.25),rgba($black,.001)),null);
}

//Iconsforwithin
.carousel-control-prev-icon,
.carousel-control-next-icon{
display:inline-block;
width:$carousel-control-icon-width;
height:$carousel-control-icon-width;
background-repeat:no-repeat;
background-position:50%;
background-size:100%100%;
}

/*rtl:options:{
"autoRename":true,
"string Map":[{
"name":"prev-next",
"search":"prev",
"replace":"next"
}]
}*/
.carousel-control-prev-icon{
background-image:escape-svg($carousel-control-prev-icon-bg);
}
.carousel-control-next-icon{
background-image:escape-svg($carousel-control-next-icon-bg);
}

//Optionalindicatorpips/controls
//
//Addacontainer(suchasalist)withthefollowingclass andaddanitem(ideallyafocusablecontrol,
//likeabutton)withdata-bs-targetforeachslideyourcarouselholds.

.carousel-indicators{
position:absolute;
right:0;
bottom:0;
left:0;
z-index:2;
display:flex;
justify-content:center;
padding:0;
//Usethe.carousel-control'swidthasmarginsowedon'toverlaythose
margin-right:$carousel-control-width;
margin-bottom:1rem;
margin-left:$carousel-control-width;
list-style:none;

[data-bs-target]{
box-sizing:content-box;
flex:01auto;
width:$carousel-indicator-width;
height:$carousel-indicator-height;
padding:0;
margin-right:$carousel-indicator-spacer;
margin-left:$carousel-indicator-spacer;
text-indent:-999px;
cursor:point er;
background-color:$carousel-indicator-active-bg;
background-clip:padding-box;
border:0;
//Usetransparentborderstoincreasethehitareaby10pxontopandbottom.
border-top:$carousel-indicator-hit-area-heightsolidtransparent;
border-bottom:$carousel-indicator-hit-area-heightsolidtransparent;
opacity:$carousel-indicator-opacity;
@includetransition($carousel-indicator-transition);
}

.active{
opacity:$carousel-indicator-active-opacity;
}
}


//Optionalcaptions
//
//

.carousel-caption{
position:absolute;
right:(100%-$carousel-caption-width)*.5;
bottom:$carousel-caption-spacer;
left:(100%-$carousel-caption-width)*.5;
padding-top:$carousel-caption-padding-y;
padding-bottom:$carousel-caption-padding-y;
color:$carousel-caption-color;
text-align:center;
}

//Darkmodecarousel

.carousel-dark{
.carousel-control-prev-icon,
.carousel-control-next-icon{
filter:$carousel-dark-control-icon-filter;
}

.carousel-indicators[data-bs-target]{
background-color:$carousel-dark-indicator-active-bg;
}

.carousel-caption{
color:$carousel-dark-caption-color;
}
}
