//Select
//
//Replacesthebrowserdefaultselectwithacustomone,mostlypulledfrom
//https://primer.github.io/.

.form-select{
display:block;
width:100%;
padding:$form-select-padding-y$form-select-indicator-padding$form-select-padding-y$form-select-padding-x;
//stylelint -disable-next-lineproperty-no-vendor-prefix
-moz-padding-start:subtract($form-select-padding-x,3px);//Seehttps://github.com/twbs/bootstrap/issues/32636
font-family:$form-select-font-family;
@includefont-size($form-select-font-size);
font-weight:$form-select-font-weight;
line-height:$form-select-line-height;
color:$form-select-color;
background-color:$form-select-bg;
background-image:escape-svg($form-select-indicator);
background-repeat:no-repeat;
background-position:$form-select-bg-position;
background-size:$form-select-bg-size;
border:$form-select-border-widthsolid$form-select-border-color;
@includeborder-radius($form-select-border-radius,0);
@includebox-shadow($form-select-box-shadow);
@includetransition($form-select-transition);
appearance:none;

&:focus{
border-color:$form-select-focus-border-color;
outline:0;
@if$enable-shadows{
@includebox-shadow($form-select-box-shadow,$form-select-focus-box-shadow);
}@else{
//Avoidusing mixinsowecanpasscustomfocusshadowproperly
box-shadow:$form-select-focus-box-shadow;
}
}

&[multiple],
&[size]:not([size="1"]){
padding-right:$form-select-padding-x;
background-image:none;
}

&:disabled{
color:$form-select-disabled-color;
background-color:$form-select-disabled-bg;
border-color:$form-select-disabled-border-color;
}

//RemoveoutlinefromselectboxinFF
&:-moz-focusring{
color:transparent;
text-shadow:000$form-select-color;
}
}

.form-select-sm{
padding-top:$form-select-padding-y-sm;
padding-bottom:$form-select-padding-y-sm;
padding-left:$form-select-padding-x-sm;
@includefont-size($form-select-font-size-sm);
}

.form-select-lg{
padding-top:$form-select-padding-y-lg;
padding-bottom:$form-select-padding-y-lg;
padding-left:$form-select-padding-x-lg;
@includefont-size($form-select-font-size-lg);
}
