.form-floating{
position:relative;

>.form-control,
>.form-select{
height:$form-floating-height;
line-height:$form-floating-line-height;
}

>label{
position:absolute;
top:0;
left:0;
height:100%;//allowtextareas
padding:$form-floating-padding-y$form-floating-padding-x;
point er-events:none;
border:$input-border-widthsolidtransparent;//Requiredforaligninglabel'stextwiththeinputasitaffectsinnerboxmodel
transform-origin:00;
@includetransition($form-floating-transition);
}

//stylelint -disableno-duplicate-selectors
>.form-control{
padding:$form-floating-padding-y$form-floating-padding-x;

&::placeholder{
color:transparent;
}

&:focus,
&:not(:placeholder-shown){
padding-top:$form-floating-input-padding-t;
padding-bottom:$form-floating-input-padding-b;
}
//Duplicatedbecause`:-webkit-autofill`invalidatesotherselectorswhengrouped
&:-webkit-autofill{
padding-top:$form-floating-input-padding-t;
padding-bottom:$form-floating-input-padding-b;
}
}

>.form-select{
padding-top:$form-floating-input-padding-t;
padding-bottom:$form-floating-input-padding-b;
}

>.form-control:focus,
>.form-control:not(:placeholder-shown),
>.form-select{
~label{
opacity:$form-floating-label-opacity;
transform:$form-floating-label-transform;
}
}
//Duplicatedbecause`:-webkit-autofill`invalidatesotherselectorswhengrouped
>.form-control:-webkit-autofill{
~label{
opacity:$form-floating-label-opacity;
transform:$form-floating-label-transform;
}
}
//stylelint -enableno-duplicate-selectors
}
