//scss-docs-startcaret-mixins
@mixincaret-down{
border-top:$caret-widthsolid;
border-right:$caret-widthsolidtransparent;
border-bottom:0;
border-left:$caret-widthsolidtransparent;
}

@mixincaret-up{
border-top:0;
border-right:$caret-widthsolidtransparent;
border-bottom:$caret-widthsolid;
border-left:$caret-widthsolidtransparent;
}

@mixincaret-end{
border-top:$caret-widthsolidtransparent;
border-right:0;
border-bottom:$caret-widthsolidtransparent;
border-left:$caret-widthsolid;
}

@mixincaret-start{
border-top:$caret-widthsolidtransparent;
border-right:$caret-widthsolid;
border-bottom:$caret-widthsolidtransparent;
}

@mixincaret($direction:down){
@if$enable-caret{
&::after{
display:inline-block;
margin-left:$caret-spacing;
vertical-align:$caret-vertical-align;
content:"";
@if$direction==down{
@includecaret-down();
}@elseif$direction==up{
@includecaret-up();
}@elseif$direction==end{
@includecaret-end();
}
}

@if$direction==start{
&::after{
display:none;
}

&::before{
display:inline-block;
margin-right:$caret-spacing;
vertical-align:$caret-vertical-align;
content:"";
@includecaret-start();
}
}

&:empty::after{
margin-left:0;
}
}
}
//scss-docs-endcaret-mixins
