//Containerwidths
//
//Setthecontainerwidth,andoverrideitforfixednavbarsinmediaqueries.

@if$enable-grid-class es{
//Singlecontainerclass withbreakpoint max-widths
.container,
//100%widecontaineratallbreakpoint s
.container-fluid{
@includemake-container();
}

//Responsivecontainersthatare100%wideuntilabreakpoint 
@each$breakpoint ,$container-max-widthin$container-max-widths{
.container-#{$breakpoint }{
@extend.container-fluid;
}

@includemedia-breakpoint -up($breakpoint ,$grid-breakpoint s){
%responsive-container-#{$breakpoint }{
max-width:$container-max-width;
}

//Extendeachbreakpoint whichissmallerorequaltothecurrentbreakpoint 
$extend-breakpoint :true;

@each$name,$widthin$grid-breakpoint s{
@if($extend-breakpoint ){
.container#{breakpoint -infix($name,$grid-breakpoint s)}{
@extend%responsive-container-#{$breakpoint };
}

//Oncethecurrentbreakpoint isreached,stopextending
@if($breakpoint ==$name){
$extend-breakpoint :false;
}
}
}
}
}
}
