//
//BasicBootstraptable
//

.table{
--#{$var iable-prefix}table-bg:#{$table-bg};
--#{$var iable-prefix}table-accent-bg:#{$table-accent-bg};
--#{$var iable-prefix}table-striped-color:#{$table-striped-color};
--#{$var iable-prefix}table-striped-bg:#{$table-striped-bg};
--#{$var iable-prefix}table-active-color:#{$table-active-color};
--#{$var iable-prefix}table-active-bg:#{$table-active-bg};
--#{$var iable-prefix}table-hover-color:#{$table-hover-color};
--#{$var iable-prefix}table-hover-bg:#{$table-hover-bg};

width:100%;
margin-bottom:$spacer;
color:$table-color;
vertical-align:$table-cell-vertical-align;
border-color:$table-border-color;

//Targetth&td
//Weneedthechildcombinatortopreventstylesleakingtonestedtableswhichdoesn'thavea`.table`class .
//Weusetheuniversalselectorsheretosimplifytheselector(elsewewouldneed6differentselectors).
//Anotheradvantageisthatthisgenerateslesscodeandmakestheselectorlessspecificmakingiteasiertooverride.
//stylelint -disable-next-lineselector-max-universal
>:not(caption)>*>*{
padding:$table-cell-padding-y$table-cell-padding-x;
background-color:var (--#{$var iable-prefix}table-bg);
border-bottom-width:$table-border-width;
box-shadow:inset0009999pxvar (--#{$var iable-prefix}table-accent-bg);
}

>tbody{
vertical-align:inherit;
}

>thead{
vertical-align:bottom;
}

//Highlightbordercolorbetweenthead,tbodyandtfoot.
>:not(:last-child)>:last-child>*{
border-bottom-color:$table-group-separator-color;
}
}


//
//Changeplacementofcaptionswithaclass 
//

.caption-top{
caption-side:top;
}


//
//Condensedtablew/halfpadding
//

.table-sm{
//stylelint -disable-next-lineselector-max-universal
>:not(caption)>*>*{
padding:$table-cell-padding-y-sm$table-cell-padding-x-sm;
}
}


//Borderversions
//
//Addorremovebordersallaroundthetableandbetweenallthecolumns.
//
//Whenbordersareaddedonallsidesofthecells,thecornerscanrenderoddwhen
//thesebordersdonothavethesamecolororiftheyaresemi-transparent.
//Thereforweaddtopandborderbottomstothe`tr`sandleftandrightborders
//tothe`td`sor`th`s

.table-bordered{
>:not(caption)>*{
border-width:$table-border-width0;

//stylelint -disable-next-lineselector-max-universal
>*{
border-width:0$table-border-width;
}
}
}

.table-borderless{
//stylelint -disable-next-lineselector-max-universal
>:not(caption)>*>*{
border-bottom-width:0;
}
}

//Zebra-striping
//
//Defaultzebra-stripestyles(alternatinggrayandtransparentbackgrounds)

.table-striped{
>tbody>tr:nth-of-type(#{$table-striped-order}){
--#{$var iable-prefix}table-accent-bg:var (--#{$var iable-prefix}table-striped-bg);
color:var (--#{$var iable-prefix}table-striped-color);
}
}

//Activetable
//
//The`.table-active`class canbeaddedtohighlightrowsorcells

.table-active{
--#{$var iable-prefix}table-accent-bg:var (--#{$var iable-prefix}table-active-bg);
color:var (--#{$var iable-prefix}table-active-color);
}

//Hovereffect
//
//Placedheresinceithastocomeafterthepotentialzebrastriping

.table-hover{
>tbody>tr:hover{
--#{$var iable-prefix}table-accent-bg:var (--#{$var iable-prefix}table-hover-bg);
color:var (--#{$var iable-prefix}table-hover-color);
}
}


//Tablevar iants
//
//Tablevar iantssetthetablecellbackgrounds,bordercolors
//andthecolorsofthestriped,hovered&activetables

@each$color,$valuein$table-var iants{
@includetable-var iant($color,$value);
}

//Responsivetables
//
//Generateseriesof`.table-responsive-*`class esforconfiguringthescreen
//sizeofwhereyourtablewilloverflow.

@each$breakpoint inmap-keys($grid-breakpoint s){
$infix:breakpoint -infix($breakpoint ,$grid-breakpoint s);

@includemedia-breakpoint -down($breakpoint ){
.table-responsive#{$infix}{
overflow-x:auto;
-webkit-overflow-scrolling:touch;
}
}
}
