//stylelint -disabledeclaration-no-important

//Hidecontentvisuallywhilekeepingitaccessibletoassistivetechnologies
//
//See:https://www.a11yproject.com/posts/2013-01-11-how-to-hide-content/
//See:https://hugogiraudel.com/2016/10/13/css-hide-and-seek/

@mixinvisually-hidden(){
position:absolute!important;
width:1px!important;
height:1px!important;
padding:0!important;
margin:-1px!important;//Fixforhttps://github.com/twbs/bootstrap/issues/25686
overflow:hidden!important;
clip:rect(0,0,0,0)!important;
white-space:nowrap!important;
border:0!important;
}

//Usetoonlydisplaycontentwhenit'sfocused,oroneofitschildelementsisfocused
//(i.e.whenfocusiswithint heelement/containerthattheclass wasappliedto)
//
//Usefulfor"Skiptomaincontent"links;seehttps://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1

@mixinvisually-hidden-focusable(){
&:not(:focus):not(:focus-within){
@includevisually-hidden();
}
}
