//
//Basestyles
//

.accordion-button{
position:relative;
display:flex;
align-items:center;
width:100%;
padding:$accordion-button-padding-y$accordion-button-padding-x;
@includefont-size($font-size-base);
color:$accordion-button-color;
text-align:left;//Resetbuttonstyle
background-color:$accordion-button-bg;
border:0;
@includeborder-radius(0);
overflow-anchor:none;
@includetransition($accordion-transition);

&:not(.collapsed){
color:$accordion-button-active-color;
background-color:$accordion-button-active-bg;
box-shadow:inset0($accordion-border-width*-1)0$accordion-border-color;

&::after{
background-image:escape-svg($accordion-button-active-icon);
transform:$accordion-icon-transform;
}
}

//Accordionicon
&::after{
flex-shrink:0;
width:$accordion-icon-width;
height:$accordion-icon-width;
margin-left:auto;
content:"";
background-image:escape-svg($accordion-button-icon);
background-repeat:no-repeat;
background-size:$accordion-icon-width;
@includetransition($accordion-icon-transition);
}

&:hover{
z-index:2;
}

&:focus{
z-index:3;
border-color:$accordion-button-focus-border-color;
outline:0;
box-shadow:$accordion-button-focus-box-shadow;
}
}

.accordion-header{
margin-bottom:0;
}

.accordion-item{
background-color:$accordion-bg;
border:$accordion-border-widthsolid$accordion-border-color;

&:first-of-type{
@includeborder-top-radius($accordion-border-radius);

.accordion-button{
@includeborder-top-radius($accordion-inner-border-radius);
}
}

&:not(:first-of-type){
border-top:0;
}

//Onlysetaborder-radiusonthelastitemiftheaccordioniscollapsed
&:last-of-type{
@includeborder-bottom-radius($accordion-border-radius);

.accordion-button{
&.collapsed{
@includeborder-bottom-radius($accordion-inner-border-radius);
}
}

.accordion-collapse{
@includeborder-bottom-radius($accordion-border-radius);
}
}
}

.accordion-body{
padding:$accordion-body-padding-y$accordion-body-padding-x;
}


//Flushaccordionitems
//
//Removebordersandborder-radiustokeepaccordionitemsedge-to-edge.

.accordion-flush{
.accordion-collapse{
border-width:0;
}

.accordion-item{
border-right:0;
border-left:0;
@includeborder-radius(0);

&:first-child{border-top:0;}
&:last-child{border-bottom:0;}

.accordion-button{
@includeborder-radius(0);
}
}
}
