//Row
//
//Rowscontainyourcolumns.

@if$enable-grid-class es{
.row{
@includemake-row();

>*{
@includemake-col-ready();
}
}
}

@if$enable-cssgrid{
.grid{
display:grid;
grid-template-rows:repeat(var (--#{$var iable-prefix}rows,1),1fr);
grid-template-columns:repeat(var (--#{$var iable-prefix}columns,#{$grid-columns}),1fr);
gap:var (--#{$var iable-prefix}gap,#{$grid-gutter-width});

@includemake-cssgrid();
}
}


//Columns
//
//Commonstylesforsmallandlargegridcolumns

@if$enable-grid-class es{
@includemake-grid-columns();
}
