//ListGroups

//scss-docs-startlist-group-mixin
@mixinlist-group-item-var iant($state,$background,$color){
.list-group-item-#{$state}{
color:$color;
background-color:$background;

&.list-group-item-action{
&:hover,
&:focus{
color:$color;
background-color:shade-color($background,10%);
}

&.active{
color:$white;
background-color:$color;
border-color:$color;
}
}
}
}
//scss-docs-endlist-group-mixin
