//
//Headings
//
.h1{
@extendh1;
}

.h2{
@extendh2;
}

.h3{
@extendh3;
}

.h4{
@extendh4;
}

.h5{
@extendh5;
}

.h6{
@extendh6;
}


.lead{
@includefont-size($lead-font-size);
font-weight:$lead-font-weight;
}

//Typedisplayclass es
@each$display,$font-sizein$display-font-sizes{
.display-#{$display}{
@includefont-size($font-size);
font-weight:$display-font-weight;
line-height:$display-line-height;
}
}

//
//Emphasis
//
.small{
@extendsmall;
}

.mark{
@extendmark;
}

//
//Lists
//

.list-unstyled{
@includelist-unstyled();
}

//Inlineturnslistitemsint oinline-block
.list-inline{
@includelist-unstyled();
}
.list-inline-item{
display:inline-block;

&:not(:last-child){
margin-right:$list-inline-padding;
}
}


//
//Misc
//

//Buildson`abbr`
.initialism{
@includefont-size($initialism-font-size);
text-transform:uppercase;
}

//Blockquotes
.blockquote{
margin-bottom:$blockquote-margin-y;
@includefont-size($blockquote-font-size);

>:last-child{
margin-bottom:0;
}
}

.blockquote-footer{
margin-top:-$blockquote-margin-y;
margin-bottom:$blockquote-margin-y;
@includefont-size($blockquote-footer-font-size);
color:$blockquote-footer-color;

&::before{
content:"\2014\00A0";//emdash,nbsp
}
}
