:root{
//Note:Customvar iablevaluesonlysupportSassScriptinside`#{}`.

//Colors
//
//Generatepalettesforfullcolors,grays,andthemecolors.

@each$color,$valuein$colors{
--#{$var iable-prefix}#{$color}:#{$value};
}

@each$color,$valuein$grays{
--#{$var iable-prefix}gray-#{$color}:#{$value};
}

@each$color,$valuein$theme-colors{
--#{$var iable-prefix}#{$color}:#{$value};
}

@each$color,$valuein$theme-colors-rgb{
--#{$var iable-prefix}#{$color}-rgb:#{$value};
}

--#{$var iable-prefix}white-rgb:#{to-rgb($white)};
--#{$var iable-prefix}black-rgb:#{to-rgb($black)};
--#{$var iable-prefix}body-rgb:#{to-rgb($body-color)};

//Fonts

//Note:Use`inspect`forlistssothatquoteditemskeepthequotes.
//Seehttps://github.com/sass/sass/issues/2383#issuecomment-336349172
--#{$var iable-prefix}font-sans-serif:#{inspect($font-family-sans-serif)};
--#{$var iable-prefix}font-monospace:#{inspect($font-family-monospace)};
--#{$var iable-prefix}gradient:#{$gradient};

//Rootandbody
//stylelint -disablecustom-property-empty-line-before
//scss-docs-startroot-body-var iables
@if$font-size-root!=null{
--#{$var iable-prefix}root-font-size:#{$font-size-root};
}
--#{$var iable-prefix}body-font-family:#{$font-family-base};
--#{$var iable-prefix}body-font-size:#{$font-size-base};
--#{$var iable-prefix}body-font-weight:#{$font-weight-base};
--#{$var iable-prefix}body-line-height:#{$line-height-base};
--#{$var iable-prefix}body-color:#{$body-color};
@if$body-text-align!=null{
--#{$var iable-prefix}body-text-align:#{$body-text-align};
}
--#{$var iable-prefix}body-bg:#{$body-bg};
//scss-docs-endroot-body-var iables
//stylelint -enablecustom-property-empty-line-before
}
