//
//Basestyles
//

.input-group{
position:relative;
display:flex;
flex-wrap:wrap;//Forformvalidationfeedback
align-items:stretch;
width:100%;

>.form-control,
>.form-select{
position:relative;//Forfocusstate'sz-index
flex:11auto;
width:1%;
min-width:0;//https://stackoverflow.com/questions/36247140/why-dont-flex-items-shrink-past-content-size
}

//Bringthe"active"formcontroltothetopofsurroundingelements
>.form-control:focus,
>.form-select:focus{
z-index:3;
}

//Ensurebuttonsarealwaysaboveinputsformorevisuallypleasingborders.
//Thisisn'tneededfor`.input-group-text`sinceitsharesthesameborder-color
//asourinputs.
.btn{
position:relative;
z-index:2;

&:focus{
z-index:3;
}
}
}


//Textualaddons
//
//Servesasacatch-allelementforanytextorradio/checkboxinputyouwish
//toprependorappendtoaninput.

.input-group-text{
display:flex;
align-items:center;
padding:$input-group-addon-padding-y$input-group-addon-padding-x;
@includefont-size($input-font-size);//Matchinputs
font-weight:$input-group-addon-font-weight;
line-height:$input-line-height;
color:$input-group-addon-color;
text-align:center;
white-space:nowrap;
background-color:$input-group-addon-bg;
border:$input-border-widthsolid$input-group-addon-border-color;
@includeborder-radius($input-border-radius);
}


//Sizing
//
//Remixthedefaultformcontrolsizingclass esint onewonesforeasier
//manipulation.

.input-group-lg>.form-control,
.input-group-lg>.form-select,
.input-group-lg>.input-group-text,
.input-group-lg>.btn{
padding:$input-padding-y-lg$input-padding-x-lg;
@includefont-size($input-font-size-lg);
@includeborder-radius($input-border-radius-lg);
}

.input-group-sm>.form-control,
.input-group-sm>.form-select,
.input-group-sm>.input-group-text,
.input-group-sm>.btn{
padding:$input-padding-y-sm$input-padding-x-sm;
@includefont-size($input-font-size-sm);
@includeborder-radius($input-border-radius-sm);
}

.input-group-lg>.form-select,
.input-group-sm>.form-select{
padding-right:$form-select-padding-x+$form-select-indicator-padding;
}


//Roundedcorners
//
//Theserulesetsmustcomeafterthesizingonestoproperlyoverridesmandlg
//border-radiusvalueswhenextending.They'remorespecificthanwe'dlike
//withthe`.input-group>`part,butwithoutit,wecannotoverridethesizing.

//stylelint -disable-next-lineno-duplicate-selectors
.input-group{
&:not(.has-validation){
>:not(:last-child):not(.dropdown-toggle):not(.dropdown-menu),
>.dropdown-toggle:nth-last-child(n+3){
@includeborder-end-radius(0);
}
}

&.has-validation{
>:nth-last-child(n+3):not(.dropdown-toggle):not(.dropdown-menu),
>.dropdown-toggle:nth-last-child(n+4){
@includeborder-end-radius(0);
}
}

$validation-messages:"";
@each$stateinmap-keys($form-validation-states){
$validation-messages:$validation-messages+":not(."+unquote($state)+"-tooltip)"+":not(."+unquote($state)+"-feedback)";
}

>:not(:first-child):not(.dropdown-menu)#{$validation-messages}{
margin-left:-$input-border-width;
@includeborder-start-radius(0);
}
}
