//Makethedivbehavelikeabutton
.btn-group,
.btn-group-vertical{
position:relative;
display:inline-flex;
vertical-align:middle;//match.btnalignmentgivenfont-sizehackabove

>.btn{
position:relative;
flex:11auto;
}

//Bringthehover,focused,and"active"buttonstothefronttooverlay
//thebordersproperly
>.btn-check:checked+.btn,
>.btn-check:focus+.btn,
>.btn:hover,
>.btn:focus,
>.btn:active,
>.btn.active{
z-index:1;
}
}

//Optional:Groupmultiplebuttongroupstogetherforatoolbar
.btn-toolbar{
display:flex;
flex-wrap:wrap;
justify-content:flex-start;

.input-group{
width:auto;
}
}

.btn-group{
//Preventdoubleborderswhenbuttonsarenexttoeachother
>.btn:not(:first-child),
>.btn-group:not(:first-child){
margin-left:-$btn-border-width;
}

//Resetroundedcorners
>.btn:not(:last-child):not(.dropdown-toggle),
>.btn-group:not(:last-child)>.btn{
@includeborder-end-radius(0);
}

//Theleftradiusshouldbe0ifthebuttonis:
//-the"thirdormore"child
//-thesecondchildandthepreviouselementisn't`.btn-check`(makingitthefirstchildvisually)
//-partofabtn-groupwhichisn'tthefirstchild
>.btn:nth-child(n+3),
>:not(.btn-check)+.btn,
>.btn-group:not(:first-child)>.btn{
@includeborder-start-radius(0);
}
}

//Sizing
//
//Remixthedefaultbuttonsizingclass esint onewonesforeasiermanipulation.

.btn-group-sm>.btn{@extend.btn-sm;}
.btn-group-lg>.btn{@extend.btn-lg;}


//
//Splitbuttondropdowns
//

.dropdown-toggle-split{
padding-right:$btn-padding-x*.75;
padding-left:$btn-padding-x*.75;

&::after,
.dropup&::after,
.dropend&::after{
margin-left:0;
}

.dropstart&::before{
margin-right:0;
}
}

.btn-sm+.dropdown-toggle-split{
padding-right:$btn-padding-x-sm*.75;
padding-left:$btn-padding-x-sm*.75;
}

.btn-lg+.dropdown-toggle-split{
padding-right:$btn-padding-x-lg*.75;
padding-left:$btn-padding-x-lg*.75;
}


//Theclickablebuttonfortogglingthemenu
//Setthesameinsetshadowasthe:activestate
.btn-group.show.dropdown-toggle{
@includebox-shadow($btn-active-box-shadow);

//Shownoshadowfor`.btn-link`sinceithasnootherbuttonstyles.
&.btn-link{
@includebox-shadow(none);
}
}


//
//Verticalbuttongroups
//

.btn-group-vertical{
flex-direction:column;
align-items:flex-start;
justify-content:center;

>.btn,
>.btn-group{
width:100%;
}

>.btn:not(:first-child),
>.btn-group:not(:first-child){
margin-top:-$btn-border-width;
}

//Resetroundedcorners
>.btn:not(:last-child):not(.dropdown-toggle),
>.btn-group:not(:last-child)>.btn{
@includeborder-bottom-radius(0);
}

>.btn~.btn,
>.btn-group:not(:first-child)>.btn{
@includeborder-top-radius(0);
}
}
