@mixinreset-text{
font-family:$font-family-base;
//WedeliberatelydoNOTresetfont-sizeoroverflow-wrap/word-wrap.
font-style:normal;
font-weight:$font-weight-normal;
line-height:$line-height-base;
text-align:left;//Fallbackforwhere`start`isnotsupported
text-align:start;
text-decoration:none;
text-shadow:none;
text-transform:none;
letter-spacing:normal;
word-break:normal;
word-spacing:normal;
white-space:normal;
line-break:auto;
}
