.popover{
position:absolute;
top:0;
left:0#{"/*rtl:ignore*/"};
z-index:$zindex-popover;
display:block;
max-width:$popover-max-width;
//Ourparentelementcanbearbitrarysincetooltipsarebydefaultinsertedasasiblingoftheirtargetelement.
//Soresetourfontandtextpropertiestoavoidinheritingweirdvalues.
@includereset-text();
@includefont-size($popover-font-size);
//Allowbreakingverylongwordssotheydon'toverflowthepopover'sbounds
word-wrap:break-word;
background-color:$popover-bg;
background-clip:padding-box;
border:$popover-border-widthsolid$popover-border-color;
@includeborder-radius($popover-border-radius);
@includebox-shadow($popover-box-shadow);

.popover-arrow{
position:absolute;
display:block;
width:$popover-arrow-width;
height:$popover-arrow-height;

&::before,
&::after{
position:absolute;
display:block;
content:"";
border-color:transparent;
border-style:solid;
}
}
}

.bs-popover-top{
>.popover-arrow{
bottom:subtract(-$popover-arrow-height,$popover-border-width);

&::before{
bottom:0;
border-width:$popover-arrow-height($popover-arrow-width*.5)0;
border-top-color:$popover-arrow-outer-color;
}

&::after{
bottom:$popover-border-width;
border-width:$popover-arrow-height($popover-arrow-width*.5)0;
border-top-color:$popover-arrow-color;
}
}
}

.bs-popover-end{
>.popover-arrow{
left:subtract(-$popover-arrow-height,$popover-border-width);
width:$popover-arrow-height;
height:$popover-arrow-width;

&::before{
left:0;
border-width:($popover-arrow-width*.5)$popover-arrow-height($popover-arrow-width*.5)0;
border-right-color:$popover-arrow-outer-color;
}

&::after{
left:$popover-border-width;
border-width:($popover-arrow-width*.5)$popover-arrow-height($popover-arrow-width*.5)0;
border-right-color:$popover-arrow-color;
}
}
}

.bs-popover-bottom{
>.popover-arrow{
top:subtract(-$popover-arrow-height,$popover-border-width);

&::before{
top:0;
border-width:0($popover-arrow-width*.5)$popover-arrow-height($popover-arrow-width*.5);
border-bottom-color:$popover-arrow-outer-color;
}

&::after{
top:$popover-border-width;
border-width:0($popover-arrow-width*.5)$popover-arrow-height($popover-arrow-width*.5);
border-bottom-color:$popover-arrow-color;
}
}

//Thiswillremovethepopover-header'sborderjustbelowthearrow
.popover-header::before{
position:absolute;
top:0;
left:50%;
display:block;
width:$popover-arrow-width;
margin-left:-$popover-arrow-width*.5;
content:"";
border-bottom:$popover-border-widthsolid$popover-header-bg;
}
}

.bs-popover-start{
>.popover-arrow{
right:subtract(-$popover-arrow-height,$popover-border-width);
width:$popover-arrow-height;
height:$popover-arrow-width;

&::before{
right:0;
border-width:($popover-arrow-width*.5)0($popover-arrow-width*.5)$popover-arrow-height;
border-left-color:$popover-arrow-outer-color;
}

&::after{
right:$popover-border-width;
border-width:($popover-arrow-width*.5)0($popover-arrow-width*.5)$popover-arrow-height;
border-left-color:$popover-arrow-color;
}
}
}

.bs-popover-auto{
&[data-popper-placement^="top"]{
@extend.bs-popover-top;
}
&[data-popper-placement^="right"]{
@extend.bs-popover-end;
}
&[data-popper-placement^="bottom"]{
@extend.bs-popover-bottom;
}
&[data-popper-placement^="left"]{
@extend.bs-popover-start;
}
}

//Offsetthepopovertoaccountforthepopoverarrow
.popover-header{
padding:$popover-header-padding-y$popover-header-padding-x;
margin-bottom:0;//ResetthedefaultfromReboot
@includefont-size($font-size-base);
color:$popover-header-color;
background-color:$popover-header-bg;
border-bottom:$popover-border-widthsolid$popover-border-color;
@includeborder-top-radius($popover-inner-border-radius);

&:empty{
display:none;
}
}

.popover-body{
padding:$popover-body-padding-y$popover-body-padding-x;
color:$popover-body-color;
}
